@use "../global/mixins.scss";

.add {
  padding-top: 29px;
  padding-bottom: 46px;
  outline: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
}

.add__title {
  margin-right: auto;
  margin-bottom: 14px;
  margin-left: auto;
  width: 176px;
  font-size: 25px;
  text-align: center;
}

.add__choice {
  margin-bottom: 20px;
  font-size: 17px;
  line-height: 30px;
  text-align: center;
}

.add__list {
  @include mixins.list-reset;
  display: flex;
  flex-wrap: wrap;
  gap: 7px;
  margin-bottom: 31px;
}

.add__mark-radio {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 82px;
  height: 52px;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  border: 2px solid #ededed;
}

.add__radio:checked + .add__mark-radio {
  border-color: var(--c-bgc-interior);
}

.add__button {
  padding-top: 8px;
  padding-bottom: 6px;
  background-color: var(--c-bgc-interior);
  color: var(--white);
  width: 260px;
  border: none;
  height: 52px;
  font-family: var(--main-font);
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
  cursor: pointer;
}
